
.em {
  display: inline-block;
  width: 1em;
}
.em_5 {
  display: inline-block;
  width: .5em;
}
body,
ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6,
dl,
dt,
dd,
input,
form {
  margin: 0px;
  padding: 0px;
  list-style: none;
}
body {
  background-color: #f0eff6;
  color: #372f2c;
  font-family: "Microsoft Yahei";
}
.fl {
  float: left;
}
.fr {
  float: right;
}
#addaddr {
  margin-top: 20px;
}
#addaddr .place {
  background: #fff;
  padding: 0 20px;
}
#addaddr .place li {
  overflow: hidden;
  padding: 10px 0;
  border-bottom: 1px solid #ddd;
}
#addaddr .place li .fr {
  color: #999;
  border: none;
  height: 25px;
  text-align: right;
}
#addaddr .place li .fr:focus {
  outline: none;
}
#addaddr .place .place-defu {
  overflow: hidden;
}
#addaddr .place .place-defu .fl .col {
  color: #999;
  margin-top: 5px;
}
#addaddr .place .place-defu .fr {
  margin-top: 5px;
  padding: 10px 0;
}
#addaddr .place .place-defu .weui-cell:before {
  border-top: none;
}
#addaddr .smit {
  margin-top: 20px;
  width: 85%;
  margin-left: auto;
  margin-right: auto;
  background: #04b101;
  color: #fff;
  border-radius: 10px;
  text-align: center;
  padding: 10px 0;
}



